@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;900&family=Poppins:wght@300;400;500;600;700;800&family=Roboto:wght@300;400;500;700;900&display=swap');

*,*::before,*::after{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
}

:root{
    --primary-hue: 210;
    --gray-hue: 240;
    --color-primary:hsl(var(--primary-hue), 100%, 50%);
    --color-primary-variant:hsl(var(--primary-hue), 100%, 65%);
    --color-secondary:#ffb116;
    --color-gray-100:hsl(var(--gray-hue), 47%, 94%);
    --color-gray-200:hsl(var(--gray-hue), 26%, 75%);
    --color-gray-300:hsl(var(--gray-hue), 24%, 64%);
    --color-gray-400:hsl(var(--gray-hue), 16%, 41%);
    --color-gray-500:hsl(var(--gray-hue), 44%, 25%);
    --color-gray-600:hsl(var(--gray-hue), 48%, 15%);


    --container-width-lg:80%;
    --container-width-md:90%;

    --transition:all 500ms ease;
}

/* GENERAL STYLES */

body{
    font-family: 'Montserrat', sans-serif;
    color: var(--color-gray-200);
    line-height: 1.7;
    overflow-x: hidden;
    background: var(--color-gray-600);
}
.container{
    width: var(--container-width-lg);
    max-width: 1920px;
    margin: 0 auto;
}
h1,h2,h3,h4,h5{
    line-height: 1.2;
    color: var(--color-gray-100);
}
h1{
    font-size: 3rem;
}
h2{
    font-style: 2rem;
}
a{
    color: var(--color-gray-100);
}
img{
    display: block;
    object-fit: cover;
    width: 100%;
}







.btn{
    color: var(--color-gray-100);
    width: fit-content;
    margin: 0 auto;
    padding: 0.9rem 2rem;
    background: var(--color-primary);
    border-radius: 1.5rem;
    transition: var(--transition);
}
.btn:hover{
    background: var(--color-secondary);
    color: var(--color-gray-600);
}
.btn.lg{
    padding: 1.2rem 3rem;
    border-radius: 2rem;
    font-size: 1.1rem;
}
.btn.sm{
    padding: 0.4rem;
    font-size: 0.9rem;
}
.btn.sm:hover{
    background: var(--color-gray-100);
}






section{
    margin-top: 10rem;
}
.section__head{
    display: flex;
    align-items: center;
    gap: 1rem;
}
.section__head span{
    background: var(--color-gray-500);
    padding: 0.8rem;
    border-radius: 1rem;
    color: var(--color-secondary);
    font-size: 1.5rem;
}






.card{
    background: var(--color-gray-500);
    border: 2px solid transparent;
    text-align: center;
    padding: 3rem 2rem ;
    border-radius: 0 3rem 0 3rem;
    transition: var(--transition);
}
.card:hover{
    background: transparent;
    border-color: var(--color-gray-400);
    cursor: default;
}
.card span{
    width: 3rem;
    height: 3rem;
    background: var(--color-primary);
    color: var(--color-gray-100);
    font-size: 1.5rem;
    padding: 0.8rem;
    display: grid;
    place-items: center;
    margin-bottom: 1.5rem;
    margin-inline: auto;
    border-radius: 1rem;
    transition: var(--transition);
}
.card:hover span{
    background: var(--color-secondary);
    color: var(--color-gray-600);
}
.card small{
    margin-top: 1rem;
    display: block;
    font-weight: 300;
}

.card a{
    margin-top: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.card:hover a{
    background: var(--color-secondary);
    color: var(--color-gray-600);
}




.card.testimonial{
    position: relative;
    text-align: left;
    margin-top: 5rem;
}

.card small.testimonial__title{
    margin-top: 0.3rem;
}

.header{
    margin-top: 5rem;
    height: 20rem;
    overflow: hidden;
    border-bottom: 2px solid var(--color-gray-400);
}
.header__container{
    width: 100%;
    height: 100%;
    position: relative;
    display: grid;
    place-items: center;
    background: black;
}
.header__container-bg{
    position: absolute;
    width: 100%;
}
.header__container-bg img{
    opacity: 0.5;
}
.header__content{
    position: relative;
    width: 44%;
    margin: 0 auto;
    text-align: center;
    color: var(--color-gray-100);
}
.header__content p{
    color: rgba(255, 255, 255, 0.7);
}

.plan p.disable{
    color: var(--color-gray-400);
}

