.hide__nav{
    display: none;
}









/* media query for medium devices */

@media screen and (max-width: 1024px) {
    .container{
        width: var(--container-width-md);
    }
    h1{
        font-size: 2rem;
    }
    h2{
        font-size: 1.6rem;
    }
    .header__content{
        width: 76%;
    }
}



/* media query for small devices */

@media screen and (max-width: 600px) {
    h1{
        font-size: 2.2rem;
        line-height: 1.3;
    }
    .section{
        margin-top: 7rem;
    }
    .header{
        height: fit-content;
    }
    .header__content{
        width: var(--container-width-md);
        padding: 3rem 0;
    }
    .header__content p{
        font-size: 0.85rem;
    }
}



    