.trainers{
    margin-top: 2rem;
}

.trainers__container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
}
.card.trainer{
    padding: 1rem;
}
.trainer__img{
    border-radius: 0 2rem ;
    overflow: hidden;
}
.trainers h3{
    margin-top: 2rem;
}
.trainers p{
    margin-top: 0.5rem;
}
.trainer__socials{
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;
}
.trainer__socials a{
    font-size: 1rem;
    background: var(--color-gray-600);
    padding: 0.8rem;
    border-radius: 0.7rem;
    transition: var(--transition);
}
.trainer:hover a{
    background: var(--color-gray-500);
}





/* media query for medium devices */

@media screen and (max-width: 1024px){
    .trainers__container{
        grid-template-columns: 1fr 1fr;
        gap: 2.5rem;
    }
}



/* media query for small devices */

@media screen and (max-width: 600px){
    .trainers__container{
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    .card.trainer{
        width: 90%;
        margin-inline: auto;
    }
}