.about__section-imag{
    border-radius: 50%;
    overflow: hidden;
    transform: skew(15deg);
    transition: var(--transition);
}
.about__section-imag:nth-child(2){
    transform: skew(-15deg);
}
.about__section-imag:hover{
    transform: skew(0);
}
.about__section-content h1{
    margin-bottom: 2rem;
}
.about__section-content p{
    margin-bottom: 1rem;
}
.about__story_container,
.about__mission_container{
    display: grid;
    grid-template-columns: 40% 50%;
    gap: 10%;
}
.about__vision_container{
    display: grid;
    grid-template-columns: 50% 40%;
    gap: 10%;
}


/* media query for medium devices */

@media screen and (max-width: 1024px) {
    .about__section-imag{
        width: 60%;
    }
    .about__section-content h1{
        margin-bottom: 1.2rem;
    }
    .about__story_container,
    .about__mission_container,
    .about__vision_container{
        grid-template-columns: 1fr;
        gap: 3rem;
    }
    .about__vision.about__section-imag{
        grid-row: 1;
    }
}



/* media query for medium devices */

@media screen and (max-width: 600px){
    .about__section-imag{
        width: 80%;
        margin-inline: auto;
    }
    .about__mission_container,
    .about__vision_container,
    .about__story_container{
        gap: 2rem;
    }
}